.dashboard{
    width: 100vw;
    max-width: 100%;
    display: grid;
    grid-template-columns: 1fr 5fr;
    height: 100vh;
    position: absolute;
}
.dashboardContainer{
    border-left: 1px solid rgba(0,0,0,0.13);
    background-color: white;
    padding: 3rem 0;
}

.dashboardContainer > h1 {
    color: rgba(0, 0, 0, 0.733);
    font: 300 2rem "Roboto" !important;
    width: 50%;
    padding: 1.5rem;
    margin: auto;
    text-align: center;
}
.dashboardSummary{
    margin: 2rem 0 ;
}
.dashboardSummary > div {
    display: flex;
    justify-content: center;
    background-color: white;
}
.dashboardSummary > div > p{
    background-color: rgba(70,117,218,0.932);
    color: white;
    width: 100%;
    margin: 0 2rem;
    text-align: center;
    padding: 1.5rem;
    font: 300 1.3rem "Roboto";
}
.dashboardSummaryBox2 > a{
    color: black;
    font: 300 2rem "Roboto";
    display: flex;
    text-align: center;
    background-color: rgb(105, 202, 76);
    color: white;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-decoration: none;
    padding: 1.5rem;
    border-radius: 100%;
    width: 10vmax;
    height: 10vmax;
    margin: 2rem;
}
.dashboardSummaryBox2 >a:first-child{
    background-color: rgb(255,110,110);
    color: white;
}
.dashboardSummaryBox2 >a:last-child{
    background-color: rgb(56, 189, 212);
    color: white;
}
.lineChart{
    width: 80%;
    margin: auto;
}
.doughnutChart{
    width:30vmax ;
    margin: auto;
}
@media screen and (max-width:600px) {
    .dashboard{
        grid-template-columns: 1fr;
    }
    .dashboardContainer{
        border-left: none;
    }
    .dashboardSummary > div > p {
        margin: 0;
    }
    .dashboardSummaryBox2 > a{
        padding: 0.5rem;
        margin: 1rem;
        font: 300 0.9rem "Roboto";
    }
}