footer{
    margin-top: 10vmax ;
    padding: 2vmax;
    background-color: black;
    color: white;
    display: flex;
    align-items: center;

}

.leftFooter{
    width: 20%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.leftFooter > h4 {
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.leftFooter > p {
    text-align: center;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
.leftFooter > img {
    width:10vmax;
    margin:1vmax;
    cursor: pointer;
}
.midFooter{
    width: 60%;
    text-align: center;
}

.midFooter > h1 {
    font-size: 4vmax;
    font-family: 'Roboto';
    color: crimson;
}

.midFooter > p {
    max-width: 60%;
    margin: 1vmax auto;
}

.rightFooter {
    width: 20%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
}

.rightFooter > h4 {
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: 1.4vmax;
    text-decoration: underline;
    letter-spacing: 1px;
}

.rightFooter a img {
    height: 50px;
    width: 50px;
}

