
.updatePasswordContainer{
    width: 100vw;
    height: 100vh;
    max-width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(231,231,231);
    position: fixed;
    top: 0%;
    left: 0;
}

.updatePasswordBox{
    background-color: white;
    width: 35vw;
    height: 70vh;
    box-sizing: border-box;
    overflow: hidden;
    padding: 5px;
}

.updatePasswordHeading{
    text-align: center;
    color: rgba(0,0,0,0.664);
    font: 500 1.3vmax "Roboto";
    padding: 1.3vmax;
    border-bottom: 1px solid rgba(0,0,0,0.265);
    width: 50%;
    margin: auto;
}

.updatePasswordForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    padding: 2vmax;
    justify-content: space-evenly;
    height: 80%;
    transition: all 0.5s;
}


#updatePasswordImage > input {
    display: flex;
    padding: 0%;
}
#updatePasswordImage > input::file-selector-button {
    cursor: pointer;
    width: 100%;
    z-index: 2;
    height: 5vh;
    border: none;
    margin: 0%;
    font: 500 1vmax cursive;
    transition: all 0.5s;
    padding: 0 1vmax;
    color: rgba(0, 0, 0, 0.623);
    background-color: rgb(255, 255, 255);
    }
    
#updatePasswordImage > input::file-selector-button:hover {
    background-color: rgb(235, 235, 235);
}




.updatePasswordForm > div{
    display: flex;
    width: 100%;
    align-items: center;
}


.updatePasswordForm > div > input {
    padding: 1vmax 4vmax;
    padding-right: 1vmax;
    width:100% ;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 0.267);
    border-radius: 4px;
    font: 500 1.4vmax cursive;
    outline: none;

}

.updatePasswordForm > div > svg {
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.6vmax;
    color: rgba(0,0,0,0.623);
}





.updatePasswordBtn {
    border: none;
    background-color: tomato;
    color: white;
    font: 300 1.2vmax "Roboto";
    letter-spacing: 1px;
    width: 80%;
    padding: 1.2vmax;
    cursor: pointer;
    transition: all 0.5s;
    border-radius: 4px;
    outline: none;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.219);
}


.updatePasswordBtn:hover {
    background-color: rgb(179, 66, 46);
}


@media screen and (max-width: 600px) {

.updatePasswordContainer {
    background-color: white;
}
.updatePasswordBox {
    width: 100vw;
    height: 95vh;
}
.updatePasswordHeading {
    font: 500 2.1vmax "Roboto";
}
.updatePasswordForm {
    padding: 5vmax;
}


.updatePasswordForm> div > input {
    padding: 2.5vmax 5vmax;
    font: 300 1.7vmax cursive;
}


.updatePasswordForm > div > svg {
    font-size: 2.8vmax;
}
.loginForm> a {
    font: 600 1.9vmax "Gill Sans";
}
#updatePasswordImage > img {
    width: 5vmax;
    margin:5px;
    margin-left: -5px;
    border-radius: 100%;
}
#updatePasswordImage > input::file-selector-button {
    height: 7vh;
    font: 400 1.8vmax cursive;
}

.updatePasswordBtn {
    font: 300 1.9vmax "Roboto";
    padding: 1.9vmax;
}


}