.orderDetailsPage{
    background-color: rgb(255, 255, 255);
}

.orderDetailsContainer > h1 {
    font: 300 3vmax "Roboto" !important;
    margin: 4vmax 0;
    color: tomato;
}
.orderDetailsContainer{
    padding: 5vmax;
    padding-bottom: 0%;
}
.orderDetailsContainer > p {
    font: 400 2vmax "Roboto" !important;
}
.orderDetailsContainerBox,
.orderDetailsCartItemsContainer{
    margin: 2vmax;
}
.orderDetailsContainerBox > div {
    display: flex;
    margin: 1vmax 0;
    
}
.orderDetailsContainerBox > div > p {
    font: 400 1vmax "Roboto";
    /* color: black; */
}
.orderDetailsContainerBox > div > span {
    margin: 0 1vmax;
    font: 200 1vmax "Roboto";
    color: #575757;
}
.orderDetailsCartItems > p {
    font: 400 2vmax "Roboto" !important;
}
.orderDetailsCartItems{
    padding: 2vmax 5vmax;
    border-top: 1px solid rgba(0,0,0,0.164);
}
.orderDetailsCartItems > div {
    display: flex;
    font: 400 1vmax "Roboto";
    align-items: center;
    margin: 2vmax 0;
    justify-content: space-between;
}

.orderDetailsCartItems > div > img {
    width: 6vmax;
}
.orderDetailsCartItems > div a {
    color: #575757;
    margin: 0 2vmax;
    width: 60%;
    text-decoration: none;
}
.orderDetailsCartItemsContainer > div > span {
    font: 100 1vmax "Roboto";
    color: #575757;
}

@media screen and (max-width:600px) {
    .orderDetailsContainer > p{
        font: 400 6vw "Roboto" !important;
    }
    .orderDetailsContainerBox > div{
        margin: 6vw 0;
    }
    .orderDetailsContainerBox > div > p{
        font: 400 4vw "Roboto" !important;
    }
    .orderDetailsContainerBox > div > span{
        font: 100 4vw "Roboto" !important;
    }
    .orderDetailsCartItems > p {
        font: 400 6vw "Roboto" !important;
    }
    .orderDetailsCartItems > div {
        font: 400 2vmax "Roboto";
        margin: 4vmax 0;
    }
    .orderDetailsCartItems > div > img {
        width: 10vmax;
    }
    .orderDetailsCartItems > div a {
        margin: 2vmax;
        width: 30%;

    }
    .orderDetailsCartItemsContainer > div > span {
        font: 100 4vmax "Roboto";
    
    }
}